import { _decorator, Component, Node, SpriteFrame, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('game_sgj')
export class game_sgj extends Component {

    //赢分、我的积分、比倍 用的数字
    @property(SpriteFrame)
    up_number_spf: SpriteFrame[] = [];

    //下注用的数字
    @property(SpriteFrame)
    dowm_number_spf: SpriteFrame[] = [];

    //赢分spr
    @property(Sprite)
    win_score_spr: Sprite[] = [];

    //我的积分spr
    @property(Sprite)
    user_score_spr: Sprite[] = [];

    //下注1分数spr
    @property(Sprite)
    bet1_score_spr: Sprite[] = [];

    //下注2分数spr
    @property(Sprite)
    bet2_score_spr: Sprite[] = [];

    //下注3分数spr
    @property(Sprite)
    bet3_score_spr: Sprite[] = [];

    //下注4分数spr
    @property(Sprite)
    bet4_score_spr: Sprite[] = [];

    //下注5分数spr
    @property(Sprite)
    bet5_score_spr: Sprite[] = [];

    //下注6分数spr
    @property(Sprite)
    bet6_score_spr: Sprite[] = [];

    //下注7分数spr
    @property(Sprite)
    bet7_score_spr: Sprite[] = [];

    //下注8分数spr
    @property(Sprite)
    bet8_score_spr: Sprite[] = [];

    //下注分数
    bet_score = [8];

    //


    start() {
        //初始化分数
        globalThis.userMgr.score = 100
        this.setUserScore(globalThis.userMgr.score);
        this.setWinScore(0);
        for (let i = 0; i < 8; i++) {
            this.bet_score[i] = 0;
        }
    }

    update(deltaTime: number) {


    }

    //设置赢分
    setWinScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                this.win_score_spr[i].spriteFrame = this.up_number_spf[st];
                this.win_score_spr[i].node.active = true;
            }
            else {
                this.win_score_spr[i].node.active = false;
            }
        }
    }

    //设置用户分数
    setUserScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                this.user_score_spr[i].spriteFrame = this.up_number_spf[st];
                this.user_score_spr[i].node.active = true;
            }
            else {
                this.user_score_spr[i].node.active = false;
            }
        }
    }

    onBtnClick(event: any, customEventData: any) {
        switch (customEventData) {
            case 'go':
                {
                    this.setUserScore(10000)
                    break;
                }
            case 'pg':
                {
                    this.setBetScore(this.bet1_score_spr, 0);
                    break;
                }
            case 'cz':
                {
                    this.setBetScore(this.bet2_score_spr, 1);
                    break;
                }
            default:
                break;
        }
    }

    //下注分数
    setBetScore(betn_node, btnindex) {
        //  if (btn == 1) {
        if (this.bet_score[btnindex] < 99 && globalThis.userMgr.score > 0) {
            this.bet_score[btnindex] += 1;
            globalThis.userMgr.score -= 1;
            // this.setBetScoreEx(betn_node, this.bet_score[btnindex])
            let str = this.bet_score[btnindex].toString();
            let len = str.length;
            for (let i = 0; i < 2; i++) {
                if (i < len) {
                    let st = Number(str[len - i - 1]);
                    betn_node[i].spriteFrame = this.up_number_spf[st];
                    betn_node[i].node.active = true;
                }
                else {
                    betn_node[i].node.active = false;
                }
            }
            this.setUserScore(globalThis.userMgr.score);
        }


        //  }
    }

    //设置下注分数
    setBetScoreEx(betnode, score: number) {

    }
}